import React, { Component } from 'react';
import './user-vacations.css';
import { VacationModel } from '../../models/vacations-model';
import { store } from '../../redux/store';
import { Unsubscribe } from 'redux';
import axios from 'axios';
import { ActionType } from '../../redux/action-type';
import { FollowersModel } from '../../models/followers-model';
import { NavLink } from 'react-router-dom';
import Checkbox from '@material/react-checkbox';
import io from 'socket.io-client';
import { Config } from '../../config';


interface UserVacationsState {
    vacations: VacationModel[],
    followers: FollowersModel,
    firstName: string,
    follow: boolean,
    allFollowers: FollowersModel,

}


export class UserVacations extends Component<any, UserVacationsState> {

    private unsubscribeStore: Unsubscribe;

    public constructor(props: any) {
        super(props);
        this.state = {
            vacations: store.getState().vacations,
            followers: new FollowersModel(),
            firstName: "",
            follow: false,
            allFollowers: new FollowersModel(),
        }
    }

    private socket = io.connect(`${Config.serverUrl}`); // Server Address.

    public clearCookies = async () => {
        this.socket.disconnect();
        sessionStorage.clear();
    }


    public componentDidMount = async () => {
        this.socket.on("we-got-a-change-from-server", vacations => {
            this.setState({ vacations })
        });
        if (sessionStorage.getItem("isAdmin")) {
            if (!sessionStorage.getItem("isAdmin").includes("1")) {
                this.props.history.push("/user-vacations");
            }
        }
        else{
            this.props.history.push("/login");
            return;
        }
        const firstSpliceName = sessionStorage.getItem("firstName");
        const firstName = firstSpliceName.split(`"`)[1];
        this.setState({ firstName })

        this.unsubscribeStore = store.subscribe(() => {
            const vacations = store.getState().vacations;
            this.setState({ vacations });
        });

        try {
            if (store.getState().vacations.length <= 0) {
                const authAxios = axios.create({
                    baseURL: `${Config.serverUrl}`,
                    headers: {
                        Authorization: `Bearer ${sessionStorage.getItem("token")}`
                    }
                });
                const response = await authAxios.get<VacationModel[]>("/api/vacations/all");
                const vacations = response.data;
                const action = { type: ActionType.GetAllVacations, payload: vacations };
                store.dispatch(action);

            }
        } catch (error) {
            this.props.history.push("/login");
            console.log("Error: " + error.message)
        }
    }


    public newFollow = async (vacationId) => {
        try {
            const followers = { ...this.state.followers };
            followers.userId = JSON.parse(sessionStorage.getItem("id"));
            followers.vacationId = vacationId;
            const authAxios = axios.create({
                baseURL: `${Config.serverUrl}`,
                headers: {
                    Authorization: `Bearer ${sessionStorage.getItem("token")}`
                }
            });
            await authAxios.post<FollowersModel>("/api/vacations/new-follower", followers);
        } catch (error) {
            console.log("Error: " + error)
        }
    }

    public disLike = async (vacationId) => {
        try {
            const followers = { ...this.state.followers };
            followers.userId = JSON.parse(sessionStorage.getItem("id"));
            followers.vacationId = vacationId;
            const authAxios = axios.create({
                baseURL: `${Config.serverUrl}`,
                headers: {
                    Authorization: `Bearer ${sessionStorage.getItem("token")}`
                }
            });
            await authAxios.post<FollowersModel>("/api/vacations/remove-follower", followers);
        } catch (error) {
            console.log("Error: " + error);
            return;
        }
    }

    public render() {
        return (
            <div className="userVacations">
                <h1>Follow Your Favorite Vacation</h1>
                <div className="user">
                    <h4>Welcome: {this.state.firstName}</h4>
                    <NavLink to="/login" onClick={this.clearCookies} >Logout</NavLink>
                </div>
                {this.state.vacations.map(v =>
                    <div className="vacations" key={v.vacationId}>
                        <img src={"/images/" + v.imageName} alt="img" />
                        <hr />
                        <h4> <strong>Description:</strong>  <br /><div className="description">{v.description}</div></h4>
                        <h4><strong>Vacation Place:</strong> <br />{v.destination}</h4>
                        <h4><strong>Vacation Start:</strong> <br />{new Date(v.startingDate).toLocaleDateString()}</h4>
                        <h4><strong>Vacation End:</strong> <br />{new Date(v.endingDate).toLocaleDateString()}</h4>
                        <h4><strong>Vacation Price:</strong> <br />{v.price}$</h4>
                        <span><strong>Vacation Followers: </strong> <br /></span>
                        <span><strong>{v.following}</strong></span>
                        <br />
                        <span><strong> Follow Vacation:</strong> <br /></span>
                        <Checkbox checked={this.state.follow} onChange={(e) => { (e.target.checked === true) ? this.newFollow(v.vacationId) : this.disLike(v.vacationId) }} />
                    </div>
                )
                }
            </div>
        )
    }
}
