import React, { Component, ChangeEvent } from 'react';
import './new-vacation.css';
import axios from 'axios';
import { Row, Col, Container, Button } from 'reactstrap';
import { VacationModel } from '../../models/vacations-model';
import { NavLink } from 'react-router-dom';
import Cookies from 'universal-cookie';
import { Config } from '../../config';

interface NewVacationState {
    newVacation: VacationModel,
    setFile: string,
}

export class NewVacation extends Component<any, NewVacationState>{
    public constructor(props: any) {
        super(props);
        this.state = {
            newVacation: new VacationModel(),
            setFile: "",
        }
    }
    public cookie = new Cookies();
    public componentDidMount() {
        if (sessionStorage.getItem("isAdmin")) {
            if (!sessionStorage.getItem("isAdmin").includes("1")) {
                this.props.history.push("/user-vacations");
            }
        }
        else{
            this.props.history.push("/login");
        }
    }
    public description = (args: ChangeEvent<HTMLTextAreaElement>) => {
        const description = args.target.value;
        const newVacation = { ...this.state.newVacation }
        newVacation.description = description;
        this.setState({ newVacation });
    }
    public destination = (args: ChangeEvent<HTMLInputElement>) => {
        const destination = args.target.value;
        const newVacation = { ...this.state.newVacation }
        newVacation.destination = destination;
        this.setState({ newVacation });
    }

    public getDateFormat = (date) => {
        const newD = new Date(date).toString()
        const nowDate = new Date(date);
        let oldStartDate = `${nowDate.getFullYear()}-${("0" + (nowDate.getMonth() + 1)).slice(-2)}-${newD.slice(8, 10)}`;
        return oldStartDate;
    };

    public startingDate = (args: ChangeEvent<HTMLInputElement>) => {
        const start = args.target.value;
        const localTime = this.getDateFormat(new Date())
        if (start < localTime) {
            alert("Cannot set Old Date ")
            return;
        }
        const newVacation = { ...this.state.newVacation }
        newVacation.startingDate = start;
        this.setState({ newVacation });
    }
    public endingDate = (args: ChangeEvent<HTMLInputElement>) => {
        const end = args.target.value;
        if (end <= this.state.newVacation.startingDate) {
            alert("Cannot set Older Date then Starting Date or Equal")
            return;
        } else if(this.state.newVacation.startingDate === undefined){
            alert("There is no starting date \nCannot set Ending Date before Starting Date")
        }else {
            const newVacation = { ...this.state.newVacation }
            newVacation.endingDate = end;
            this.setState({ newVacation });
        }
    }
    public price = (args: ChangeEvent<HTMLInputElement>) => {
        const price = +args.target.value;
        const newVacation = { ...this.state.newVacation }
        newVacation.price = price;
        this.setState({ newVacation });
    }

    public imageChange = (event) => {
        this.setState({ setFile: event.target.files[0] });
        const newVacation = { ...this.state.newVacation }
        newVacation.imageName = event.target.value;
        this.setState({ newVacation });
    }

    public newVacation = async () => {
        try {
            const data = new FormData();

            if (this.state.newVacation.destination === undefined) {
                alert("missing Destination");
                return;
            }
            if (this.state.newVacation.price === undefined) {
                alert("missing Price");
                return;
            }
            if (this.state.newVacation.startingDate === undefined) {
                alert("missing Starting Date");
                return;
            }
            if (this.state.newVacation.endingDate === undefined) {
                alert("missing Ending Date");
                return;
            }

            if (this.state.newVacation.imageName === undefined) {
                alert("missing Image");
                return;
            }
            if (this.state.newVacation.description === undefined) {
                alert("missing Description");
                return;
            }
                data.append("file", this.state.setFile);
                const authAxios = axios.create({
                    baseURL:`${Config.serverUrl}`,
                    headers: {
                        Authorization:`Bearer ${sessionStorage.getItem("token")}`
                    }
                });
                this.props.history.push("admin");
                await authAxios.post("/api/vacations/new-vacation", this.state.newVacation);
                await authAxios.post("/api/vacations/new-image", data);

            } catch (error) {
                console.log(error.message)
            }
    }

    public render() {
        return (
            <div className="newVacation">
                <Container>
                    <br />
                    <h2>Create New Vacation</h2>
                    <br />
                    <Row md="2" xs="2" lg="1">
                        <Col><input type="text" placeholder=" Vacation Place: Where is it?" onChange={this.destination} /></Col>
                        <Col><input type="number" placeholder=" Price: How much it cost? in $" onChange={this.price} /></Col>
                        <Col><label >Vacation Start: </label></Col>
                        <Col><input type="date" onChange={this.startingDate} /></Col>
                        <Col><label >Vacation End: </label></Col>
                        <Col><input type="date" onChange={this.endingDate} /></Col>
                        <Col><label >Vacation Image: </label ></Col>
                        <Col><input type="file" onChange={this.imageChange} accept=".jpg"/></Col>
                        <Col><label >Description: </label></Col>
                        <Col><textarea onChange={this.description} placeholder=" Keep it simple max Length 1500 keys" /></Col>
                        <Col><Button color="primary" onClick={this.newVacation}>Submit new Vacation</Button></Col>
                        <Col><div className="back"><NavLink color="black" to="/admin">Go Back</NavLink></div></Col>
                        <Col><br /></Col>
                    </Row>
                </Container>
            </div>
        );
    }
}