import React, { Component } from 'react';
import './layout.css';
import { BrowserRouter, Switch, Route, } from 'react-router-dom';
import { LoginPage } from '../login-page/login-page';
import { Register } from '../register/register';
import { UserVacations } from '../user-vacations/user-vacations';
import { AdminPage } from '../admin-page/admin-page';
import { PageNotFound } from '../page-not-found/page-not-found';
import { NewVacation } from '../new-vaction/new-vacation';
import { Graph } from '../graph/graph';

export class Layout extends Component {
    public render() {
        return (
            <div className="layout">
                <BrowserRouter>
                    <h1>Traveler</h1>
                    <hr />
                    <Switch>
                        <Route path="/login" component={LoginPage} exact />
                        <Route path="/register" component={Register} exact />
                        <Route path="/user-vacations" component={UserVacations} exact />
                        <Route path="/admin" component={AdminPage} exact />
                        <Route path="/new-vacation" component={NewVacation} exact />
                        <Route path="/graph" component={Graph} exact />
                        <Route path="/" component={LoginPage} exact />
                        <Route path="*" component={PageNotFound} />
                    </Switch>
                </BrowserRouter>

            </div>
        )
    }
}
