import { AppState } from "./app-state";
import { Action } from "./action";
import { ActionType } from "./action-type";

export function reducer(oldAppState: AppState, action: Action): AppState {

    const newAppState = { ...oldAppState };
    switch (action.type) {

        case ActionType.GetAllVacations:
            newAppState.vacations = action.payload;
            break;
        case ActionType.DeleteVacation:
            newAppState.vacations.splice(action.payload, 1);
            break;
        case ActionType.AddNewVacation:
            newAppState.vacations.push(action.payload);
            break;
      
        default:
            break;

    }

    return newAppState;
}
