import React, { useState, useEffect } from "react";
import { Bar } from "react-chartjs-2";
import axios from "axios";
import { NavLink } from 'react-router-dom'
import { Container, Button } from "reactstrap";
import { VacationModel } from "../../models/vacations-model";
import { Config } from "../../config";


export const Graph = () => {
    const [chartData, setChartData] = useState({});
    const chart = async () => {
        const following = [];
        const vacationName = [];
        const authAxios = axios.create({
            baseURL: `${Config.serverUrl}`,
            headers: {
                Authorization: `Bearer ${sessionStorage.getItem("token")}`
            }
        });
        await authAxios.get<VacationModel[]>("/api/vacations/all")
            .then(res => {
                for (const dataObj of res.data) {
                    following.push(parseInt(dataObj.following.toString()));
                    vacationName.push(dataObj.destination);
                }
                setChartData({
                    labels: vacationName,
                    datasets: [
                        {
                            label: "Following",
                            data: following,
                            backgroundColor: ["snow", "magenta", "green", "black", "white", "orange", "brown", "gray", "red", "blue"],
                            borderWidth: 1,
                        }
                    ]
                });
            })
            .catch(err => {
                console.log(err);
            });
    };

    useEffect(() => {
        chart();
    }, []);
    return (
        <Container>
            <br />
            <h2>Vacations Following Graph</h2>
            <br />
            <div>
                <Bar
                    data={chartData}
                    options={{
                        responsive: true,
                        scales: {
                            yAxes: [
                                {
                                    ticks: {
                                        fontSize: 20,
                                        autoSkip: true,
                                        maxTicksLimit: 3,
                                        fontColor: "black",
                                        fontStyle: "bold",
                                        beginAtZero: true
                                    },
                                    gridLines: {
                                        color: "orange",
                                        display: true
                                    }
                                }
                            ],
                            xAxes: [
                                {
                                    ticks: {
                                        fontColor: "black",
                                        fontSize: 15,
                                    },
                                    gridLines: {
                                        color: "black",
                                        display: false
                                    }
                                }
                            ]
                        }
                    }}
                />
            </div>
            <br /><br />
            <NavLink to="/admin"> <Button color="primary">Back to Edit</Button></NavLink>
        </Container>
    );
};
