import React, { Component } from 'react';
import './page-not-found.css';

export class PageNotFound extends Component<any> {
    public redirect() {
        setTimeout(() => { this.props.history.push("/login") }
            , 3000);
    }
    public render() {
        return (
            <div className="pageNotFound">
                <h1>Wrong Path You Are Moving Back</h1>
                <br />
                <br />
                <h3>404</h3>
                {this.redirect()}
            </div>
        )
    }
}
