import React, { Component, ChangeEvent } from 'react';
import './register.css';
import { RegisterModel } from '../../models/register-model';
import axios from 'axios';
import { NavLink } from 'react-router-dom';

interface RegisterState {
    register: RegisterModel
}

export class Register extends Component<any, RegisterState> {
    public constructor(props: any) {
        super(props);
        this.state = {
            register: new RegisterModel("", "", "", "")
        }
    }
    public firstNameChange = (args: ChangeEvent<HTMLInputElement>) => {
        const firstName = args.target.value;
        const register = { ...this.state.register };
        register.firstName = firstName;
        this.setState({ register })
    }

    public lastNameChange = (args: ChangeEvent<HTMLInputElement>) => {
        const lastName = args.target.value;
        const register = { ...this.state.register };
        register.lastName = lastName;
        this.setState({ register })
    }

    public userNameChange = (args: ChangeEvent<HTMLInputElement>) => {
        const userName = args.target.value;
        const register = { ...this.state.register };
        register.username = userName;
        this.setState({ register })
    }

    public passwordChange = (args: ChangeEvent<HTMLInputElement>) => {
        const password = args.target.value;
        const register = { ...this.state.register };
        register.password = password;
        this.setState({ register })
    }

    public registration = async () => {
        if (!this.state.register.firstName) {
            alert("Missing First Name");
            return;
        }
        if (!this.state.register.lastName) {
            alert("Missing Last Name");
            return;
        }
        if (!this.state.register.username) {
            alert("Missing User Name");
            return;
        }
        if (!this.state.register.password) {
            alert("Missing Password");
            return;
        }
        await axios.post("http://localhost:3000/api/auth/register", this.state.register)
            .then(res => { sessionStorage.setItem("token", res.data.token) })
            .catch(err => console.log(err));
        this.props.history.push("/login")
        return;
    }


    public render() {
        return (
            <div className="register">
                <br />
                <h4>Please Fill All The Information Below:</h4>
                <input type="text" placeholder="First Name .." value={this.state.register.firstName} onChange={this.firstNameChange} />
                <br />
                <br />
                <input type="text" placeholder="Last Name .." value={this.state.register.lastName} onChange={this.lastNameChange} />
                <br />
                <br />
                <input type="text" placeholder="UserName .." value={this.state.register.username} onChange={this.userNameChange} />
                <br />
                <br />
                <input type="password" placeholder="Password .." value={this.state.register.password} onChange={this.passwordChange} />
                <br />
                <br />
                <button onClick={this.registration}> Register </button>
                <br />
                <br />
                <NavLink to="/login"> Already Have An Account?</NavLink>
            </div>
        )
    }
}
