import React, { Component, ChangeEvent } from 'react';
import { LoginModel } from '../../models/login-model';
import axios from 'axios';
import './login-page.css';
import { NavLink } from 'react-router-dom';
import Cookies from 'universal-cookie';
import { Config } from '../../config';

export interface LoginPageState {
    login: LoginModel;
}

export class LoginPage extends Component<any, LoginPageState>{

    public constructor(props: any) {
        super(props);
        this.state = {
            login: new LoginModel("", "")
        }
    }

    private cookie = new Cookies();

    public componentDidMount = () => {
        if (sessionStorage.getItem("isAdmin")) {
            if (sessionStorage.getItem("isAdmin").includes("1")) {
                this.props.history.push("/admin");
            }
        }
        if (sessionStorage.getItem("isAdmin")) {
            if (sessionStorage.getItem("isAdmin").includes("0")) {
                this.props.history.push("/user-vacations");
            }
        }
        else{
            sessionStorage.clear();
        }
    }
    public userNameChange = (args: ChangeEvent<HTMLInputElement>) => {
        const userNameCheck = args.target.value;
        const login = { ...this.state.login };
        login.username = userNameCheck;
        this.setState({ login })
    }
    public PasswordChange = (args: ChangeEvent<HTMLInputElement>) => {
        const passwordCheck = args.target.value;
        const login = { ...this.state.login };
        login.password = passwordCheck;
        this.setState({ login })
    }

    public sendInfo = async () => {
        try {
            const authAxios = axios.create({
                baseURL: `${Config.serverUrl}`,
                headers: {
                    Authorization: `Bearer ${sessionStorage.getItem("token")}`
                }
            });
            await authAxios.post("/api/auth/login", this.state.login).then(response => {
                sessionStorage.setItem("token", response.data.token);
                sessionStorage.setItem("isAdmin", JSON.stringify(response.data.user.isAdmin));
                sessionStorage.setItem("firstName", JSON.stringify(response.data.user.firstName));
                sessionStorage.setItem("id", (response.data.user.userId));
                if (response.data.user.isAdmin === 1) {
                    this.props.history.push("/admin");
                    return;
                }
                this.props.history.push("/user-vacations");
                return;

            })
        } catch (error) {
            alert("Wrong Information")
        }
    }

    public render() {
        return (
            <div className="loginPage">
                <h3> Log In / Register</h3>
                <input type="text" placeholder="User Name .." onChange={this.userNameChange} value={this.state.login.username} />
                <br />
                <br />
                <input type="password" placeholder="Password .." onChange={this.PasswordChange} value={this.state.login.password} />
                <br />
                <br />
                <button onClick={this.sendInfo}> Login </button>
                <br />
                <h4> Don't Have An Account? </h4>
                <NavLink to="/register">Register</NavLink>
            </div>
        )
    }
}
